<template>
	<view class="doubleButton">
		<button @click="clickLeftEvent" style="background: #f3f3f3; color: #0078d4; width: 350rpx; margin-right: 10rpx">
			{{leftName}}
		</button>
		<button @click="clickRightEvent" style="background: #0078d4; width: 350rpx; color: #fff">
			{{rightName}}
		</button>
	</view>
</template>

<script>
	export default {
		name: "wDoubleButton",
		props: {
			leftName: {
				type: String,
				default: '取消'
			},
			rightName:{
				type: String,
				default: '确定'
			},
		},
		data() {
			return {

			};
		},
		methods: {
			clickLeftEvent() {
				this.$emit('clickLeft')
			},
			clickRightEvent() {
				this.$emit('clickRight')
			},
		},
	}
</script>

<style lang="scss" scoped>
	.doubleButton {
		padding: 20rpx;
		display: flex;
		font-size: 30rpx;
		text-align: center;
		align-items: center;
	}
</style>
